import config from "@/config";
import Centrifuge from "centrifuge";
let socketIns = null;
export default {
  /**
   * 
   * @param {socket} api 
   * @param {socket} params 
   * @param {} cb 
   * @param {} key 
   */
  getWebSocket(api, params, cb, obj) {
    if ("WebSocket" in window && !self.connectStatus) {
      api(params).then(res => {
        const sign = res.data.access_token; //  secretusertimestamphashmapsha256
        const timestamp = res.data.timestamp; // 
        const user = res.data.user; // id
        if (!user) return
        socketIns = new Centrifuge({
          url: config.baseUrl.socket,
          user: user,
          timestamp: timestamp,
          token: sign
        });
        console.log(socketIns);

        // Websocket,
        socketIns.connect();
        // msg:userid 
        socketIns.subscribe(
          // `msg:${self.$store.state.user.userID}`,
          `{obj.key}:${obj.val}`,
          message => {
            /**
             * userID  
             * @message {object}: 
             */
            cb();
          }
        );

        socketIns.on("connect", context => {
          // console.log("", context);
        });

        socketIns.on("disconnect", context => {
          // console.log("", context);
        });

        socketIns.on("error", error => {
          // console.log("", error);
        });

        // websocketserver
        window.onbeforeunload = () => {
          socketIns.disconnect();
        };
      });
      return socketIns
    }
    return socketIns
  },
  closeWs() {
    if (socketIns) {
      socketIns.disconnect();
    }
  }
}
